.Wrapper {
  display: flex;
  align-items: flex-start;
  gap: 30px;
  padding: 2% 0;
}
.Wrapper a {
  text-decoration: none;
}
.products {
  width: 80%;
  display: flex;
  align-items: stretch !important;
  flex-direction: column;
  gap: 30px;
}
.product {
  width: 96%;
  border: 1px solid #eeeeee;
  padding: 2%;
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  border-radius: 20px;
  gap: 20px;
}
.info {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  width: 75%;
}
.count {
  display: flex;
  align-items: center;
  gap: 10px;
}
.count button {
  background-color: transparent;
  border: 1px solid #eeeeee;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
}
.texts {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.texts h3 {
  font-family: "SF Pro Display";
  font-size: 20px;
  font-weight: 500;
  letter-spacing: 0em;
}
.price {
  font-family: "SF Pro Display";
  font-size: 14px;
  font-weight: 600;
  letter-spacing: 0em;
  color: #1CA3EC;
  margin: 0;
}
.monthly {
  font-family: "SF Pro Display";
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  letter-spacing: 0em;
  margin: 1% 0;
  padding: 5px 10px;
  color: #ff7b00;
  border: 2px solid #ff7b00;
  border-radius: 30px;
}
.count {
  margin-top: 30px;
}

.buttons {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 10px;
  height: 100% !important;
}
.icon {
  border: 1px solid #eeeeee;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  cursor: pointer;
}
.iconHeart {
  border: 1px solid #eeeeee;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  cursor: pointer;
}

.icons i {
  cursor: pointer;
}
.mainPrice {
  border: 1px solid #eeeeee;
  border-radius: 30px;
  padding: 2%;
}

.mainPrice ul li {
  display: flex;
  align-items: center;
  gap: 10px;
}

.mainPrice ul li h3 {
  font-family: "SF Pro Display";
  font-size: 12px;
  font-weight: 500;
  letter-spacing: 0em;
  color: #0f2e4a;
}

.mainPrice ul {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.prices {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-top: 50px;
}
.prices p {
  font-family: "SF Pro Display";
  font-size: 11px;
  font-weight: 400;
  line-height: 13px;
  letter-spacing: 0em;
  color: #0f2e4a;
  opacity: 0.3;
  margin: 0;
}
.prices h3 {
  margin: 2% 0;
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 0em;
  font-family: "SF Pro Display";
  color: #0f2e4a;
}

.total {
  border-top: 1px solid #eeeeee;
  margin: 5% 0;
}
.total p {
  font-family: "SF Pro Display";
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0em;
  opacity: 0.3;
  margin: 5% 0 2%;
}
.total h3 {
  margin: 0;
  font-family: "SF Pro Display";
  font-size: 24px;
  font-weight: 600;
  letter-spacing: 0em;
  color: #1CA3EC;
}

.mainPrice button {
  border: 1px solid #1CA3EC;
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0em;
  width: 100%;
  margin-top: 5%;
  padding: 10px 20px;
  border-radius: 14px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  color: white;
  background-color: #1CA3EC;
}
.mediaButtons {
  display: none;
}
.icons {
  display: flex;
  align-items: end;
  justify-content: flex-end;
  gap: 10px;
}

.product img {
  width: 200px;
  height: 100%;
  margin: auto auto;
}

@media screen and (max-width: 1140px) {
  .Wrapper {
    padding: 2%;
  }
}

@media screen and (max-width: 790px) {
  .Wrapper {
    display: flex;
    flex-direction: column;
  }
  .products {
    width: 100%;
  }
  .mainPrice {
    width: 96%;
  }
}

@media screen and (max-width: 500px) {
  .product {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
  }
  .product img {
    margin: 0 auto;
  }
  .buttons {
    display: none;
  }
  .mediaButtons {
    display: flex;
    align-items: center;
    gap: 10px;
    justify-content: flex-end;
    width: 100%;
  }
}
