.mainPrice {
  border: 1px solid #eeeeee;
  border-radius: 30px;
  padding: 2%;
}

.mainPrice ul li {
  display: flex;
  align-items: center;
  gap: 10px;
}

.mainPrice ul li h3 {
  font-family: "SF Pro Display";
  font-size: 12px;
  font-weight: 500;
  letter-spacing: 0em;
  color: #0f2e4a;
}

.mainPrice ul {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.prices {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-top: 50px;
}
.prices p {
  font-family: "SF Pro Display";
  font-size: 11px;
  font-weight: 400;
  line-height: 13px;
  letter-spacing: 0em;
  color: #0f2e4a;
  opacity: 0.3;
  margin: 0;
}
.prices h3 {
  margin: 2% 0;
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 0em;
  font-family: "SF Pro Display";
  color: #0f2e4a;
}

.total {
  border-top: 1px solid #eeeeee;
  margin: 5% 0;
}
.total p {
  font-family: "SF Pro Display";
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0em;
  opacity: 0.3;
  margin: 5% 0 2%;
}
.total h3 {
  margin: 0;
  font-family: "SF Pro Display";
  font-size: 24px;
  font-weight: 600;
  letter-spacing: 0em;
  color: #1CA3EC;
}

.total button {
  border: 1px solid #1CA3EC;
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0em;
  width: 100%;
  margin-top: 5%;
  padding: 10px 0px;
  border-radius: 14px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  color: white;
  background-color: #1CA3EC;
}

.Wrapper {
  display: flex;
  align-items: flex-start;
  padding: 2% 0%;
  justify-content: space-between;
  gap: 30px;
}
.allProductsPostDiv {
  overflow: hidden;
  height: 0px;
}
.Completeform {
  width: 80%;
  border: 1px solid #eeeeee;
  padding: 4%;
  border-radius: 30px;
}
.Completeform h2 {
  font-family: "SF Pro Display";
  font-size: 42px;
  font-weight: 600;
  letter-spacing: 0em;
}

.infoText {
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0em;
  margin-top: 3%;
}
.fio {
  display: flex !important;
  gap: 30px;
}
.fio div {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.Wrapper label {
  margin: 2%;
  font-family: "SF Pro Display";
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0em;
}

.Wrapper input {
  width: 250px;
  padding: 12px 10px;
  border: 1px solid #eeeeee;
  border-radius: 14px;
  outline: none;
}
.Wrapper select {
  width: 270px;
  padding: 12px 10px;
  border: 1px solid #eeeeee;
  border-radius: 14px;
  background-color: transparent;
  outline: none;
}

.contactDiv {
  margin-top: 2%;
  display: flex;
  flex-direction: column;
  /* align-items: flex-start; */
}
.contactDiv h3 {
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0em;
}
.Wrapper label span {
  color: transparent;
}

.numbers {
  display: flex;
  align-items: flex-start;
  gap: 30px;
}
.numbers div {
  width: 270px;
}
.location {
  display: flex;
  align-items: flex-start !important;
  gap: 30px;
  margin-top: 30px;
}

.location div {
  width: 270px;
}

.comments {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
}
.comments label {
  margin: 1% 0.5% 0 !important;
}

.comments textarea {
  resize: none;
  width: 550px !important;
  border: 1px solid #eeeeee;
  border-radius: 14px;
  padding: 10px;
  outline: none;
}

.Wrapper button {
  background-color: #1CA3EC;
  color: white;
  display: flex;
  align-items: center;
  gap: 10px;
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0em;
  padding: 10px;
  border-radius: 14px;
  margin-top: 30px;
  border: 1px solid #1CA3EC;
  cursor: pointer;
}
.Wrapper button a {
  color: white;
  text-decoration: none;
}

@media screen and (max-width: 1440px) {
  .Wrapper {
    padding: 2% !important;
  }
}

@media screen and (max-width: 1068px) {
  .mainPrice {
    display: none;
  }
  .Completeform {
    width: 100%;
  }
}
@media screen and (max-width: 650px) {
  .fio {
    display: flex;
    flex-direction: column !important;
  }
  .fio input {
    width: 96% !important;
  }
  .numbers {
    display: flex;
    flex-direction: column;
  }
  .numbers div {
    width: 100%;
  }
  .numbers input {
    width: 96% !important;
  }
  .location {
    display: flex;
    flex-direction: column;
  }
  .location div {
    width: 100%;
    display: flex;
    flex-direction: column;
  }
  .location select {
    width: 99.9% !important;
  }
  .location input {
    width: 96% !important;
  }
  .comments textarea {
    width: 96% !important;
  }
  .Wrapper h2 {
    font-size: 32px;
  }
  .comments label {
    margin: 2% !important;
  }
}
