.Wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 50px;
  height: 70vh;
}

.texts h3 {
  font-family: "SF Pro Display";
  font-size: 28px;
  font-weight: 500;
  letter-spacing: 0em;
  color: #0f2e4a;
  margin: 3% 0;
}
.Wrapper a {
  text-decoration: none;
}
.texts p {
  margin: 0;
  font-family: "SF Pro Display";
  font-size: 11px;
  font-weight: 400;
  letter-spacing: 0em;
  color: #0f2e4a;
  opacity: 0.3;
}

.texts button {
  border: 2px solid #1CA3EC;
  background-color: transparent;
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 10px 20px;
  border-radius: 14px;
  margin-top: 6%;
}

.texts button svg {
  color: #1CA3EC !important;
}

.texts button span {
  color: #1CA3EC;
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0em;
}
@media screen and (max-width: 720px) {
  .Wrapper {
    padding: 0 2%;
    display: flex;
    flex-direction: column;
  }
}
