.accordions button {
  border: none;
  background-color: transparent;
  border-bottom: 1px solid #eeeeee;
  margin: 1% 0;
  padding: 2% 0;
}

.accordions {
  width: 50%;
}

.accordions .chakra-accordion__icon {
  border: 1px solid transparent;
  border-radius: 50%;
  transition: 0.65s !important;
}

.accordions .css-1v8c247 {
  border: 1px solid #1CA3EC !important;
  color: #1CA3EC;
  border-radius: 50%;
  transition: 0.65s;
}

@media screen and (max-width: 1115px) {
  .accordions {
    width: 100%;
  }
}
