.Wrapper {
  padding: 10% 0;
}
.Wrapper ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start, center;
  align-items: center;
  gap: 50px;
  padding: 0 2%;
}
.Wrapper img {
  width: 40px;
  height: 40px;
}
.Wrapper h2 {
  font-family: "SF Pro Display";
  font-size: 24px;
  font-weight: 500;
  letter-spacing: 0em;
  margin: 0% 0 3%;
}
.Wrapper ul li {
  display: flex;
  align-items: center;
  gap: 20px;
}

.imgDiv {
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f5f5f5;
  border-radius: 50%;
}

.texts p {
  margin: 0;
  font-family: "SF Pro Display";
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0px;
  color: #b7c1c9;
}

.texts h3 {
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0px;
  color: #0f2e4a;
  margin: 3% 0;
}

@media screen and (max-width: 768px) {
  .Wrapper {
    display: none;
  }
}
