.Slider button {
  display: none !important;
}
.Slider {
  width: 80% !important;
}
.Slider .splide {
  padding: 0 !important;
}
@media screen and (max-width: 1235px) {
  .Slider {
    width: 70% !important;
  }
}
@media screen and (max-width: 1050px) {
  .Slider {
    width: 100% !important;
  }
}
