.tab .ant-tabs-ink-bar {
  display: none !important;
}

.ant-tabs-nav::before {
  display: none !important;
}

.tab .ant-tabs-content-holder {
  display: none !important;
}
.tab .ant-tabs-tab-btn {
  border: 2px solid #eeeeee;
  padding: 10px 20px;
  border-radius: 14px;
  font-family: "SF Pro Display";
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0em;
}
.tab .ant-tabs-tab-btn:hover {
  color: black !important;
}

.tab .ant-tabs-tab-active .ant-tabs-tab-btn {
  color: #1CA3EC !important;
  border: 2px solid #1CA3EC;
}
