.WrapperBack {
  background-color: #f5f5f5;
  padding: 1% 0;
}

.Wrapper {
  display: flex;
  gap: 30px;
}

.Slide {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: white;
  border-radius: 30px;
  padding: 7% 5%;
  position: relative;
}
.discount {
  background: rgba(154, 163, 59, 0.8);
  backdrop-filter: blur(5px);
  transform: rotate(-20deg);
  position: absolute;
  transform: rotate(-30deg);
  left: 60%;
  padding: 3% 4%;
  color: white;
  border-radius: 50%;
  top: 50%;
}
.discount p {
  margin: 0;
  font-family: "SF Pro Display";
  font-size: 32px;
  font-weight: 600;
  letter-spacing: 0em;
}
.Slide img {
  width: 45%;
}

.texts h2 {
  font-family: "SF Pro Display";
  font-size: 42px;
  font-weight: 600;
  letter-spacing: 0em;
}

.texts p {
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 400;
  line-height: 23px;
  letter-spacing: 0em;
  color: #a5a3a1;
}
.texts .Price {
  font-family: "SF Pro Display";
  font-size: 18px;
  font-weight: 600;
  letter-spacing: 0em;
  color: #1ca3ec;
  display: flex;
  align-items: center;
  gap: 10px;
}
.Price svg {
  width: 20px;
  height: 20px;
}
.TimeProduct {
  background-image: url(../../../assets/home/background.png);
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 30px;
  overflow: hidden;
  padding: 2% 2%;
  width: 18% !important;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}

.TimeProduct h3 {
  margin: 0;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header h3 {
  color: white;
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0px;
}
.texts {
  width: 100%;
}
.header p {
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0px;
  color: #1ca3ec;
  background-color: white;
  padding: 2%;
  border-radius: 30px;
}

.TimeProduct h2 {
  font-family: "SF Pro Display";
  font-size: 28px;
  font-weight: 500;
  letter-spacing: 0em;
  color: white;
}
.priceSubtext {
  color: white !important;
  margin: 0;
  font-size: 15px;
  font-weight: 600;
  line-height: 23px;
  letter-spacing: 0em;
}
.TimeProduct img {
  width: 100%;
  border-radius: 30px;
}

@media screen and (max-width: 1235px) {
  .WrapperBack {
    padding: 1%;
  }
  .TimeProduct {
    width: 28% !important;
  }
  .Slide {
    padding: 10% 5%;
  }
}

@media screen and (max-width: 1050px) {
  .Wrapper {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  .TimeProduct {
    width: 96% !important;
    display: flex;
    flex-direction: row;
    gap: 30px;
    padding: 2% 2%;
  }
  .text {
    width: 70%;
  }
  .TimeProduct img {
    width: 300px !important;
    height: 100%;
  }
  .texts h2 {
    font-size: 30px;
  }
}

@media screen and (max-width: 550px) {
  .TimeProduct img {
    width: 150px !important;
    height: 150px !important;
  }
  .discount p {
    font-size: 13px;
  }
  .Slide {
    padding: 5%;
  }
  .Price {
    font-size: 9px !important;
  }
  .texts h2 {
    font-size: 14px !important;
  }
  .text h2 {
    font-size: 15px;
  }
  .priceSubtext {
    font-size: 10px;
  }
  .texts {
    width: 50% !important;
  }
  .texts p {
    font-size: 15px;
  }
}
