.Wrapper {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  margin-top: 100px;
}

.Wrapper img {
  width: 130px;
}

.aside .icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  border: 1px solid #eeeeee;
  border-radius: 20px;
}
.Wrapper a {
  text-decoration: none;
  color: #787472;
}
.icon svg {
  width: 20px;
  height: 20px;
}
.aside svg path {
  width: 10px !important;
  height: 10px !important;
}

.aside img {
  width: 20px;
  height: 20px;
}
.aside {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 20px;
}
.aside .icons {
  display: flex;
  align-items: center;
  gap: 20px;
}

.catelogue h2 {
  font-family: "SF Pro Display";
  font-size: 24px;
  font-weight: 500;
  line-height: 29px;
  letter-spacing: 0em;
}
.catelogue {
  margin-top: 38px;
}
.catelogue ul {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  width: 70%;
  height: 100%;
  gap: 24px;
  margin-top: 10px;
}
.catelogue ul li h3 {
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
  letter-spacing: 0em;
  color: #8d8a88;
  margin: 0;
}
.info {
  margin-top: 22px;
}
.info h2 {
  font-family: "SF Pro Display";
  font-size: 24px;
  font-weight: 500;
  letter-spacing: 0em;
}

.info ul {
  display: flex;
  align-items: center;
  gap: 24px;
}

.info ul li h3 {
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 0em;
  color: #8d8a88;
}

.BottomFooter {
  border-top: 1px solid #dfdfdf;
  margin-top: 20px;
}
.BottomFooter ul {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 30px;
  padding: 3% 0;
}
.BottomFooter ul li p {
  margin: 0;
  color: #787472;
  font-family: "SF Pro Display";
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0em;
}

@media screen and (max-width: 720px) {
  .Wrapper {
    display: flex;
    flex-wrap: wrap;
    padding: 2%;
  }
  .BottomFooter {
    margin: 5% 0;
    text-align: center;
  }
  .BottomFooter ul {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }
  .catelogue ul {
    display: flex;
    flex-wrap: wrap;
  }
  .info ul {
    display: flex;
    flex-wrap: wrap;
    gap: 0px;
  }
}
