.prducts {
  margin: 10px 0;
}
.prducts ul {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-top: 3%;
}
.prducts ul li {
  border: 1px solid #eeeeee;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  cursor: pointer;
}
.prduct img {
  width: clamp(150px, 100%, 200px);
}
.prduct .imgDiv {
  position: relative;
  border: 1px solid #eeeeee;
  padding: 5%;
  border-radius: 20px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.imgDiv p {
  position: absolute;
  left: 0;
  top: 0;
  margin: 0;
  background-color: #ff7b00;
  padding: 2% 5%;
  color: white;
  border-end-end-radius: 20px;
  font-family: "SF Pro Display";
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0em;
}

.info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2%;
}

.texts h2 {
  font-family: "SF Pro Display";
  font-size: 20px;
  font-weight: 500;
  letter-spacing: 0em;
}
.texts {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.texts .CardPrice {
  font-family: "SF Pro Display";
  font-size: 14px;
  font-weight: 600;
  letter-spacing: 0em;
  color: #1CA3EC;
}

.monthly {
  border: 2px solid #ff7b00;
  margin: 0;
  padding: 3% 5%;
  text-align: center;
  border-radius: 30px;
  color: #ff7b00;
  font-family: "SF Pro Display";
  font-size: 12px !important;
  font-weight: 500;
  width: 100%;
}

.prduct svg {
  width: 25px;
  height: 25px;
}
.prduct {
  margin: 10px 0;
}

.icon {
  width: 40px;
  height: 40px;
  border: 1px solid #eeeeee;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  cursor: pointer;
}
