.Wrapper {
  display: flex;
  gap: 30px;
  padding: 2% 0%;
}

.galery {
  width: 50%;
  display: flex;
  align-items: flex-start;
  gap: 20px;
  border: 1px solid #eeeeee;
  border-radius: 30px;
  padding: 2%;
}
.characteristics a {
  color: #1CA3EC;
  text-decoration: none;
}
.miniGalery {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.miniGalery div {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100px;
  border-radius: 50%;
  height: 100px;
  overflow: hidden;
  border: 1px solid #eee;
}
.miniGalery img {
  /* width: 85%; */
  /* height: 100px; */
}

.information h2 {
  font-family: "SF Pro Display";
  font-size: 28px;
  font-weight: 500;
  letter-spacing: 0em;
}

.info {
  display: flex;
  /* align-items: center; */
  justify-content: space-between !important;
  width: 100%;
  margin: 5% 0;
}
.info ul {
  margin: 0;
  padding: 0;
  /* border: 1px solid black; */
  display: flex;
  flex-direction: column;
  gap: 20px;
  justify-content: space-between;
}
.info ul li {
  /* border: 1px solid black; */
  display: flex;
  flex-direction: column;
  gap: 5px;
}
.info ul li p {
  color: #c1c1c1;
  font-family: "SF Pro Display";
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0em;
  margin: 0 !important;
  padding: 0 !important;
}
.info ul li h3 {
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0em;
  margin: 0% 0;
}
.characteristics {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}

.characteristics h3 {
  color: #1CA3EC;
  margin: 0;
  border-bottom: 2px solid #1CA3EC;
}

.price {
  color: #1CA3EC;
  font-family: "SF Pro Display";
  font-size: 24px;
  font-weight: 600;
  letter-spacing: 0em;
}
.price span {
  color: #c1c1c1;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0em;
  position: relative;
}
.price span::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 0.5px;
  background-color: #c1c1c1;
  left: 0;
  top: 55%;
}

.monthly {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.monthly h3 {
  font-family: "SF Pro Display";
  font-size: 12px;
  font-weight: 500;
  letter-spacing: 0em;
  color: #ff7b00;
  border: 2px solid #ff7b00;
  margin: 0;
  padding: 1% 2%;
  border-radius: 30px;
}
.request {
  margin: 5% 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.request h3 {
  margin: 0;
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0em;
  display: flex;
  align-items: center;
  gap: 7px;
}

.request p {
  margin: 0;
  font-family: "SF Pro Display";
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0em;
  color: #c1c1c1;
}
.request form {
  margin: 2% 0;
  border: 1px solid #c1c1c1;
  border-radius: 15px;
  padding: 0.5%;
  overflow: hidden;
}

.request form input {
  padding: 10px 10px;
  border: none;
  outline: none;
}

.request form button {
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0em;
  height: 100%;
  background-color: transparent;
  border: 1px solid #c1c1c1;
  border-radius: 10px;
  padding: 10px 20px;
}
.plus {
  display: flex;
  align-items: center;
  gap: 10px;
}
.plus button:nth-child(1) {
  border: 2px solid #1CA3EC;
  color: #1CA3EC;
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0em;
  display: flex;
  align-items: center;
  gap: 5px;
  padding: 10px 20px;
  border-radius: 14px;
  background-color: transparent;
}
.plus button:nth-child(2) {
  border: 2px solid #ff7b00;
  color: #ff7b00;
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 500;
  padding: 10px 20px;
  border-radius: 14px;
  background-color: transparent;
}

.icon {
  border: 1px solid #c1c1c1;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
}
.line {
  display: none;
}
@media screen and (max-width: 1175px) {
  .Wrapper {
    display: flex;
    flex-direction: column;
    padding: 2%;
  }
  .galery {
    width: 96%;
    padding: 2%;
  }
  .info {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .info ul {
    width: 100%;
  }
  .info ul li {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 0;
    margin: 0;
    height: 50px;
  }
  .line {
    display: block;
    border: 1px dashed #c1c1c1;
    width: 60%;
  }
  .info h3 {
    text-align: end;
  }
  .request {
    border: 1px solid #c1c1c1;
    padding: 2%;
    width: 96%;
    border-radius: 14px;
  }
  .request form {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

@media screen and (max-width: 620px) {
  .galery {
    display: flex;
    flex-direction: column;
  }
  .miniGalery {
    display: flex !important;
    flex-direction: row !important;
    align-items: center;
    justify-content: space-between;
    gap: 10px;
    width: 100%;
  }
}

@media screen and (max-width: 450px) {
  .information h2 {
    font-size: 24px;
  }
  .plus button {
    font-size: 14px !important;
  }
}
