.Wrapper a {
  text-decoration: none;
  color: black;
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0em;
}
.Wrapper ul {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.Wrapper ul li:nth-child(10) a {
  color: #1CA3EC;
  display: flex;
  align-items: center;
  gap: 10px;
}
.Wrapper ul li svg {
  width: 20px;
  height: 20px;
}
.WrapperBack {
  padding: 1.5% 0;
}

@media screen and (max-width: 980px) {
  .WrapperBack {
    display: none;
  }
}
