.Wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.WrapperBack {
  margin: 5% 0;
  background-color: #f5f5f5;
}

.Wrapper img {
  width: 80px;
  border-radius: 50%;
}
.texts {
  width: 40%;
}

.texts h2 {
  font-family: "SF Pro Display";
  font-size: 24px;
  font-weight: 500;
  letter-spacing: 0em;
}
.texts p {
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 0em;
  font-family: "SF Pro Display";
  color: #333333;
  opacity: 0.7;
}

.col {
  padding: 10% 10% 15% !important;
  /* margin: 0 !important; */
  padding-bottom: 42.5% !important;
  background-color: #ffffff;
}

.slider {
  position: relative;
  display: flex;
  gap: 5px;
  width: 60% !important;
}

.forBack {
  height: 100px;
  width: 100% !important;
  background: linear-gradient(180deg, #ffffff 0%, rgba(246, 245, 243, 0) 100%);
  left: 0;
  top: 0;
  z-index: 1000;
  position: absolute;
  background-repeat: no-repeat;
  background-size: 100%;
}

.forBack2 {
  height: 100px;
  width: 100% !important;
  background: linear-gradient(180deg, #f5f5f5 0%, rgba(246, 245, 243, 0) 100%);
  transform: matrix(1, 0, 0, -1, 0, 0);
  left: 0;
  bottom: 0;
  z-index: 1000;
  position: absolute;
  background-repeat: no-repeat;
  background-size: 100%;
}

.head {
  display: flex;
  align-items: center;
  gap: 20px;
}
.head h3 {
  margin: 0;
}

.head p {
  margin: 2% 0 0;
}

.subtext {
  font-family: "SF Pro Display";
  font-size: 18px;
  font-weight: 400;
  letter-spacing: 0em;
}

@media screen and (max-width: 1150px) {
  .Wrapper {
    display: flex;
    flex-direction: column;
  }
  .texts {
    width: 100%;
    padding: 5% 0% 5% 2%;
  }
  .slider {
    width: 90% !important;
  }
}

@media screen and (max-width: 762px) {
  .slider {
    width: 100% !important;
  }
}

@media screen and (max-width: 550px) {
  .Slier1 {
    display: none;
  }
  .slider {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
