.tabs {
  padding: 1% 0;
  width: 20%;
  border-right: 1px solid #eeeeee !important;
  overflow-y: scroll;
  height: 60vh;
}
.tabs::-webkit-scrollbar {
  display: none !important;
}

.tabs .ant-tabs-content-holder {
  border: none !important;
}

.tabs .ant-tabs-ink-bar-animated {
  width: 10px !important;
  background-color: #1CA3EC !important;
}

.tabs .ant-tabs-left {
  display: contents;
}
.tabs .ant-tabs-tab-btn {
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0px;
}
.tabs .ant-tabs-tab-btn:hover {
  color: black !important;
}

.tabs .ant-tabs-tab-active .ant-tabs-tab-btn {
  color: #1CA3EC !important;
}
@media screen and (max-width: 1115px) {
  .tabs {
    width: 30%;
  }
}
