.Wrapper {
  display: flex;
  align-items: flex-start;
  gap: 30px;
  padding: 2% 0%;
}

.Changes {
  width: 30%;
}

.TimeProduct {
  background-image: url(../../assets/home/background.png);
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 30px;
  overflow: hidden;
  padding: 8% !important;
  margin-top: 30px;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}

.TimeProduct h3 {
  margin: 0;
}

.texts {
  width: 100%;
}
.header p {
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0px;
  color: #1CA3EC;
  background-color: white;
  padding: 2%;
  border-radius: 30px;
}

.TimeProduct h2 {
  font-family: "SF Pro Display";
  font-size: 28px;
  font-weight: 500;
  letter-spacing: 0em;
  color: white;
}
.priceSubtext {
  color: white !important;
  margin: 0;
  font-size: 15px;
  font-weight: 600;
  line-height: 23px;
  letter-spacing: 0em;
}
.TimeProduct img {
  width: 200px;
  border-radius: 30px;
  margin: 10px auto 0px;
}

.TimeProduct button {
  background-color: transparent;
  border: 1px solid white;
  color: white;
  margin-top: 8%;
  padding: 15px 0;
  border-radius: 14px;
  font-size: 16px;
  font-family: "SF Pro Display";
  font-weight: 500;
}

@media screen and (max-width: 1115px) {
  .Wrapper {
    display: flex;
    flex-direction: column;
    padding: 2%;
  }
  .Changes {
    width: 100%;
    height: auto;
  }
  .time {
    display: none;
  }
}
