.Wrapper {
  border: 1px solid #eeeeee;
  padding: 2%;
  border-radius: 30px;
}

.Wrapper p {
  width: 50%;
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 400;
  line-height: 30px;
  letter-spacing: 0em;
}

@media screen and (max-width: 1175px) {
  .Wrapper p {
    width: 100%;
  }
}
