.Wrapper {
  display: flex;
  gap: 10px;
  padding: 20px 10px;
}

.AllChanges {
  width: 25%;
}
.prducts {
  width: 75%;
}
.categories {
  display: flex;
  flex-direction: column;
  gap: 15px;
  border: 1px solid #eeeeee;
  padding: 7%;
  border-radius: 30px;
}
.categories h2 {
  color: #1f1914;
  font-size: 20px;
  font-family: "SF Pro Display";
  font-weight: 600;
}
.headCategory {
  display: flex;
  align-items: center;
  gap: 12px;
}

.categories button svg {
  width: 20px;
  height: 20px;
}

.categories button {
  border: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: transparent;
  color: #0f2e4a;
  font-size: 16px;
  font-family: "SF Pro Display";
  font-weight: 500;
  transition: 0.65s;
}
.categories button:hover {
  color: #c50026;
}

.priceChange {
  display: flex;
  flex-direction: column;
  gap: 15px;
  border: 1px solid #eeeeee;
  padding: 7%;
  border-radius: 30px;
  margin-top: 30px;
}

.priceChange h2 {
  color: #000;
  font-size: 20px;
  font-family: "SF Pro Display";
  font-weight: 500;
}

.Wrapper .ant-slider-track-1 {
  color: #c50026 !important;
  background-color: #c50026 !important;
}

.priceChange .prices {
  display: flex;
  align-items: center;
}
.prices p {
  border: 1px solid #eeeeee;
  margin: 0;
  padding: 5% 0;
  text-align: center;
  width: 100%;
}
.prices p:nth-child(1) {
  border-start-start-radius: 12px;
  border-end-start-radius: 12px;
}

.prices p:nth-child(2) {
  border-start-end-radius: 12px;
  border-end-end-radius: 12px;
}

.priceChange input {
  padding: 10px 0px;
  margin: 10px 0;
  outline: none;
  border: none;
}

.logo {
  border: 1px solid #eeeeee;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
  cursor: pointer;
}
.logo img {
  width: 30px !important;
  height: 30px !important;
}
.headProducts {
  display: flex;
  gap: 10px;
}
.weirdBox svg {
  width: 25px;
  height: 25px;
}
.mediaControls {
  display: none;
}
.mediaControls select {
  color: #2f2f2f;
  font-size: 16px;
  font-family: "SF Pro Display";
  font-weight: 500;
  border: 1px solid #eeeeee;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 20px;
  border-radius: 12px;
  gap: 6px;
}
.mediaControls p {
  color: #2f2f2f;
  font-size: 16px;
  font-family: "SF Pro Display";
  font-weight: 500;
  border: 1px solid #eeeeee;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 20px;
  border-radius: 12px;
  gap: 6px;
}
.Filter {
  display: none;
}
.head {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.head button {
  color: #1ca3ec;
  background-color: transparent;
  border: none;
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 10px;
  cursor: pointer;
}
.mediaControls input {
  padding: 12px 20px;
  outline: none;
  border: 1px solid #eeeeee;
  border-radius: 14px;
}
.mediaControls button {
  border: 1px solid #c50026;
  color: #c50026;
  padding: 10px 20px;
  border-radius: 14px;
  background-color: transparent;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
}
.subcategory {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.minMax {
  display: flex;
  align-items: center;
  gap: 10px;
}
@media screen and (max-width: 980px) {
  .AllChanges {
    display: none;
  }
  .prducts {
    width: 100%;
  }
  .mediaControls {
    display: flex;
    align-items: center;
    gap: 20px;
    height: auto;
    padding: 0% 0 3%;
  }
  .prducts {
    padding: 2% 0 0%;
  }
}

@media screen and (max-width: 680px) {
  .Filter {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    width: 100%;
    border: 1px solid #eeeeee;
    background-color: transparent;
    padding: 15px 0px;
    cursor: pointer;
    border-radius: 14px;
    color: #2f2f2f;
    font-size: 16px;
    font-family: "SF Pro Display";
    font-weight: 500;
    margin-bottom: 20px;
  }
  .mediaControls {
    display: none;
  }
  .headProducts {
    display: none;
  }
}
