.Wrapper h2 {
  display: flex;
  align-items: center;
  gap: 10px;
  font-family: "SF Pro Display";
  font-size: 24px;
  font-weight: 500;
  letter-spacing: 0em;
  margin: 3% 0;
}
.Wrapper {
  padding: 2% 0;
}

.Col {
  margin-bottom: 20px;
}
.Wrapper svg {
  width: 25px;
  height: 25px;
  cursor: pointer;
}
.Col a {
  text-decoration: none;
  color: black;
}
.Wrapper img {
  /* width: clamp(206px, 100%, 205px); */
  /* width: 80% !important; */
  /* height: 206px !important; */
}
.Col .imgDiv {
  position: relative;
  border: 1px solid #eeeeee;
  padding: 5%;
  border-radius: 20px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.imgDiv p {
  position: absolute;
  left: 0;
  top: 0;
  margin: 0;
  background-color: #ff7b00;
  padding: 2% 5%;
  color: white;
  border-end-end-radius: 20px;
  font-family: "SF Pro Display";
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0em;
}

.info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2%;
}

.texts h2 {
  font-family: "SF Pro Display";
  font-size: 20px;
  font-weight: 500;
  letter-spacing: 0em;
}
.texts {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 5px;
}

.texts .CardPrice {
  font-family: "SF Pro Display";
  font-size: 14px;
  font-weight: 600;
  letter-spacing: 0em;
  color: #1CA3EC;
  margin: 0;
}

.monthly {
  border: 2px solid #ff7b00;
  margin: 0;
  padding: 3% 5%;
  text-align: center;
  border-radius: 30px;
  color: #ff7b00;
  font-family: "SF Pro Display";
  font-size: 12px !important;
  font-weight: 500;
  width: 100%;
}

.Col button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

@media screen and (max-width: 500px) {
  .Col {
    margin-top: 5%;
  }
  .Wrapper h2 {
    padding: 2% 2% 0%;
  }
}
