.userInformation {
  padding: 2%;
  border: 1px solid #eeeeee;
  width: 100%;
  border-radius: 30px;
}
.editUser {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}
.editUser img {
  width: 90px;
  height: 90px;
  border-radius: 50%;
}

.editUser .info {
  display: flex;
  align-items: center;
  gap: 20px;
}
.texts h3 {
  font-family: "SF Pro Display";
  font-size: 28px;
  font-weight: 500;
  letter-spacing: 0em;
  margin: 0;
}
.texts p {
  font-family: "SF Pro Display";
  font-size: 11px;
  font-weight: 400;
  letter-spacing: 0em;
  margin: 0;
  color: #c1c1c1;
  padding: 0;
}

.userInformation button {
  color: #1CA3EC;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.safeBtn {
  display: flex;
  align-items: center;
  gap: 10px;
  color: #1CA3EC;
  border: none;
  background-color: transparent;
  cursor: pointer;
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0em;
}

.personalInfo {
  margin-top: 50px;
}

.personalInfo h3 {
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0em;
}
.personalInfo form {
  display: flex;
  flex-direction: column;
  gap: 30px;
}
.personalInfo .nameEmail {
  display: flex;
  gap: 30px;
  width: 100%;
}

.nameEmail label {
  font-family: "SF Pro Display";
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0em;
  color: #c1c1c1;
  margin: 2%;
}
.nameEmail div {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.personalInfo .numberPassword {
  display: flex;
  gap: 30px;
}

.numberPassword label {
  font-family: "SF Pro Display";
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0em;
  color: #c1c1c1;
  margin: 2%;
}
.numberPassword div {
  display: flex;
  flex-direction: column;
}

.personalInfo form input {
  padding: 12.5px;
  border-radius: 10px;
  border: 1px solid #eeeeee;
  outline: none;
  width: 260px;
}
.editPassword h3 {
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0em;
}

.inputs {
  display: flex;
  gap: 30px;
}
.inputs div {
  display: flex;
  flex-direction: column;
}
.inputs label {
  font-family: "SF Pro Display";
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0em;
  margin: 2%;
}

.imgDiv {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.65s;
}
.imgDiv:hover > .box {
  display: flex !important;
}
.box {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 90px;
  height: 90px;
  border-radius: 50%;
  align-items: center;
  justify-content: center;
  transform: translate(-50%, -50%);
  background-color: #c1c1c157;
  cursor: pointer;
  display: none;
  transition: 0.65s;
}
.box ion-icon {
  font-size: 50px;
}

@media screen and (max-width: 975px) {
  .userInformation {
    width: 96%;
  }
}

@media screen and (max-width: 670px) {
  .nameEmail {
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  .userInformation form input {
    width: 98% !important;
    padding: 12.5px 0px 12.5px 10px !important;
  }
  .numberPassword {
    display: flex;
    flex-direction: column;
  }
  .inputs {
    display: flex;
    flex-direction: column;
  }
  .userInformation {
    padding: 3%;
    width: 94% !important;
  }
  .userInformation img {
    width: 50px;
    height: 50px;
  }
  .userInformation h3 {
    font-size: 20px;
  }
}
