.Wrapper {
  display: flex;
  align-items: flex-start;
  gap: 30px;
  padding: 2% 0;
}
.controlBox {
  width: 25%;
  border: 1px solid #eeeeee;
  padding: 2%;
  border-radius: 30px;
}
.user {
  display: flex;
  align-items: center;
  gap: 20px;
  margin: 0 0 15px 0;
}
.userInfo h3 {
  font-family: "SF Pro Display";
  font-size: 18px;
  font-weight: 600;
  letter-spacing: 0em;
  margin: 0;
}
.userInfo p {
  font-family: "SF Pro Display";
  font-size: 11px;
  font-weight: 400;
  letter-spacing: 0em;
  color: #c1c1c1;
  margin: 3% 0;
}
.controlBox .userImg {
  width: 70px;
  height: 70px;
  border-radius: 50%;
}

.controlBox button {
  margin-top: 50px;
  border: none;
  cursor: pointer;
  background-color: transparent;
  display: flex;
  align-items: center;
  gap: 10px;
  color: #ff0000;
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
  letter-spacing: 0em;
}


@media screen and (max-width: 1440px) {
  .Wrapper {
    padding: 2%;
  }
}

@media screen and (max-width: 975px) {
  .Wrapper {
    display: flex;
    flex-direction: column;
  }
  .controlBox {
    border: none;
    width: 100%;
    padding: 0 !important;
  }
  .user {
    display: none;
  }
  .controlBox button {
    display: none;
  }
  .userInformation {
    width: 96%;
  }
}

@media screen and (max-width: 670px) {
  .nameEmail {
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  .userInformation form input {
    width: 98% !important;
    padding: 12.5px 0px 12.5px 10px !important;
  }
  .numberPassword {
    display: flex;
    flex-direction: column;
  }
  .inputs {
    display: flex;
    flex-direction: column;
  }
  .userInformation {
    padding: 3%;
    width: 94% !important;
  }
  .userInformation img {
    width: 50px;
    height: 50px;
  }
  .userInformation h3 {
    font-size: 20px;
  }
}
