.splideBox .splide {
  padding-top: 0;
  padding-bottom: 0;
}

.splideBox .splide img {
  /* width: 100%; */
  /* height: 474px; */
}

.splideBox .splide__arrow--next {
  border: 1px solid #eeeeee;
  border-radius: 50%;
  width: 35px;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: black !important;
  margin-left: auto !important;
}
.splideBox .splide__arrow--next svg {
  fill: black !important;
  width: 20px;
  height: 20px;
}

.splideBox .splide__arrow--prev {
  border: 1px solid #eeeeee;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: black !important;
  margin-left: auto !important;
}
.splideBox .splide__arrow--prev svg {
  fill: black !important;
  width: 20px;
  height: 20px;
}

@media screen and (max-width: 1175px) {
  .splideBox {
    width: 60%;
    margin: 0 auto;
  }
}

@media screen and (max-width: 620px) {
  .splideBox {
    width: 90%;
  }
}
