.productUser {
  border: 1px solid #eeeeee;
  padding: 2%;
  display: flex;
  align-items: center;
  gap: 30px;
  border-radius: 30px;
  width: 96%;
}
.products {
  overflow-y: scroll;
  height: 250px !important;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}
.products::-webkit-scrollbar {
  display: none;
}
.Wrapper {
  width: 85%;
  display: flex;
  flex-direction: column;
  gap: 30px;
}
.Wrapper img {
  width: 70px;
  height: 70px;
}
.products {
  width: 50%;
  display: flex;
  flex-direction: column;
  gap: 20px;
  border-right: 1px dashed #dddddd;
}
.info {
  display: flex;
  align-items: center;
  gap: 10px;
}

.allInfor {
  width: 50% !important;
}
.texts h3 {
  font-family: "SF Pro Display";
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 0em;
  margin: 0;
}

.texts p {
  font-family: "SF Pro Display";
  font-size: 14px;
  font-weight: 600;
  letter-spacing: 0em;
  margin: 2% 0;
  color: #1CA3EC;
}

.actions {
  display: flex;
  align-items: center;
  gap: 10px;
}

.loading {
  font-family: "SF Pro Display";
  font-size: 12px;
  font-weight: 500;
  letter-spacing: 0em;
  color: #ff7b00;
  border: 1px solid #ff7b00;
  padding: 5px 10px;
  border-radius: 30px;
}
.number {
  font-family: "SF Pro Display";
  font-size: 12px;
  font-weight: 500;
  letter-spacing: 0em;
  color: black;
  border: 1px solid #dddddd;
  padding: 5px 10px;
  border-radius: 30px;
}

.allInfor h3 {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
  width: 100%;
}

.allInfor h3 span:nth-child(1) {
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 0em;
  color: #121212;
  opacity: 0.3;
  padding: 0;
  width: 25%;
}

.allInfor h3 div {
  width: 50%;
  border: 1px dashed #dddddd;
}

.allInfor h3 .spanText {
  font-family: "SF Pro Display";
  font-size: 16px !important;
  font-weight: 400;
  letter-spacing: 0em;
  padding: 0;
  width: 25%;
  text-align: end;
}

.buttons button {
  border: 1px solid #dddddd;
  background-color: transparent;
  color: black;
  padding: 10px;
  border-radius: 14px;
  display: flex;
  align-items: center;
  gap: 10px;
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0em;
}

.oj {
  border: 1.7px solid #ff6e38;
  color: #ff6e38 !important;
  padding: 5px 10px;
  border-radius: 100px;
  font-size: 12px;
  font-weight: 600;
}
