  color: #2ebd87 !important;
  padding: 5px 10px;
  border-radius: 100px;
  font-size: 12px;
  font-weight: 600;
}
.nep {
  border: 1.7px solid #ff0000;
  color: #ff0000 !important;
  padding: 5px 10px;
  border-radius: 100px;
  font-size: 12px;
  font-weight: 600;
}
.op {
  border: 1.7px solid #2ebd87;
  color: #2ebd87 !important;
  padding: 5px 10px;
  border-radius: 100px;
  font-size: 12px;
  font-weight: 600;
}
.ot {
  border: 1.7px solid #c9c9c9;
  color: #c9c9c9;
  padding: 5px 10px;
  border-radius: 100px;
  font-size: 12px;
  font-weight: 600;
}

@media screen and (max-width: 975px) {
  .Wrapper {
    width: 100%;
  }
}

@media screen and (max-width: 570px) {
  .productUser {
    display: flex;
    flex-direction: column;
  }
  .products {
    width: 100%;
    border: none;
  }
  .allInfor {
    width: 100% !important;
  }
}
