.Wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.Wrapper p {
  text-align: center;
  font-family: "SF Pro Display";
  font-size: 28px;
  font-weight: 500;
  line-height: 28px;
  letter-spacing: 0em;
}

.Wrapper p span {
  color: #2ebd87;
}

.Wrapper a {
  color: #1CA3EC;
  text-decoration: none;
}
.Wrapper button {
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0em;
  border: 1px solid #1CA3EC;
  color: #1CA3EC;
  background-color: transparent;
  padding: 10px 20px;
  margin: 0 auto;
  border-radius: 14px;
  cursor: pointer;
}

@media screen and (max-width: 500px) {
  .Wrapper p {
    font-size: 20px;
  }
  .Wrapper img {
    width: 100px;
  }
}
