.AboutTabs {
  padding: 10% 4% 25%;
  width: 92%;
  border-radius: 30px;
  border: 1px solid #eeeeee !important;
}

.AboutTabs .ant-tabs-content-holder {
  border: none !important;
}

.AboutTabs .ant-tabs-ink-bar-animated {
  display: none !important;
}

.AboutTabs .ant-tabs-left {
  display: contents;
}
.AboutTabs .ant-tabs-tab-btn {
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0px;
}
.AboutTabs .ant-tabs-tab-btn:hover {
  color: black !important;
}

.AboutTabs .ant-tabs-tab-active .ant-tabs-tab-btn {
  color: #1CA3EC !important;
}
@media screen and (max-width: 1115px) {
  .AboutTabs {
    width: 100%;
    padding: 0%;
    border: none !important;
  }
  .AboutTabs .ant-tabs-tab-btn {
    border: 1px solid #eeeeee;
    padding: 10px 20px;
    border-radius: 14px;
  }
  .AboutTabs .ant-tabs-tab-active .ant-tabs-tab-btn {
    color: #1CA3EC !important;
    border: 1px solid #1CA3EC;
  }
}
