.Wrapper {
  border: 1px solid #eeeeee;
  padding: 5%;
  border-radius: 30px;
  width: 95%;
}

.Wrapper h2 {
  font-family: "SF Pro Display";
  font-size: 42px;
  font-weight: 600;
  letter-spacing: 0em;
}

.subtext {
  font-family: "SF Pro Display";
  font-size: 17px;
  font-weight: 400;
  letter-spacing: 0em;
  width: 50%;
  line-height: 25.5px;
}

.description {
  font-family: Times New Roman;
  font-size: 17px;
  font-style: italic;
  font-weight: 400;
  line-height: 22px;
  letter-spacing: 0em;
  width: 50%;
}

.questions {
  margin-top: 100px;
}

.muchAsked {
  font-family: "SF Pro Display";
  font-size: 28px;
  font-weight: 500;
  letter-spacing: 0em;
  margin: 1% 0;
}
.secondSubtext {
  margin: 0;
  font-size: 11px;
  font-weight: 400;
  line-height: 16px;
  letter-spacing: 0em;
  margin-bottom: 5%;
}

.accordionSubtext {
  font-family: Times New Roman;
  font-size: 17px;
  font-style: italic;
  font-weight: 400;
  line-height: 22px;
  letter-spacing: 0em;
}

.questions h2 {
  font-size: 17px;
  font-weight: 400;
  letter-spacing: 0em;
}

@media screen and (max-width: 1115px) {
  .subtext {
    width: 100%;
  }
  .description {
    width: 100%;
  }
  .Wrapper {
    width: 94% !important;
    padding: 3%;
    margin-top: 0%;
  }
}
