.Wrapper {
  position: absolute;
  background-color: white;
  width: 300px;
  box-shadow: -23px 31px 104px 0px #3e39701f;
  right: 3%;
  z-index: 10000;
  top: 15%;
  padding: 2%;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.Wrapper button a {
  text-decoration: none;
  color: white;
}
.Form {
  display: flex;
  flex-direction: column !important;
  gap: 20px;
}
.Form div {
  display: flex;
  flex-direction: column;
}
.Form input {
  border: 1px solid #1ca3ec;
  outline: none;
  padding: 8px !important;
  border-radius: 5px;
  font-size: 14px;
  -webkit-appearance: none !important;
  -moz-appearance: textfield !important;
}
.Wrapper h2 {
  font-family: "SF Pro Display";
  font-size: 28px;
  font-weight: 500;
  letter-spacing: 0em;
}

.Wrapper label {
  font-family: "SF Pro Display";
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0em;
  color: #c8c8c8;
  margin: 2%;
}
.timer {
  color: #1ca3ec;
  text-align: start !important;
  margin-top: 10px !important;
}

.Wrapper .button {
  border: 1.8px solid #1ca3ec;
  padding: 10px 0px 13px;
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0em;
  color: #1ca3ec;
  background-color: transparent;
  border-radius: 14px;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  gap: 10px;
}
.inputPassowrd input {
  border: none;
  width: 80% !important;
}
.inputPassowrd {
  display: flex !important;
  flex-direction: row !important;
  align-items: center;
  justify-content: space-between;
  border: 1px solid #c8c8c8;
  padding: 2px 10px;
  border-radius: 5px;
}
.Form img {
  margin: 0 auto;
}
.Form h3 {
  text-align: center;
  margin: 0;
  font-family: "SF Pro Display";
  font-size: 28px;
  font-weight: 500;
  letter-spacing: 0em;
}
.Form p {
  font-family: "SF Pro Display";
  font-size: 14px;
  font-weight: 400;
  line-height: 17px;
  letter-spacing: 0em;
  margin: 0;
  text-align: center;
}
.btn a {
  color: #1ca3ec !important;
}
.subtext {
  text-align: start !important;
  font-family: "SF Pro Display";
  font-size: 14px;
  font-weight: 400;
  color: #1ca3ec;
  border: none !important;
  padding: 0 !important;
  background-color: transparent;
  cursor: pointer;
}

.login_btn {
  border: 1.5px solid #1ca3ec;
  border-radius: 10px;
  padding: 10px 0px;
  font-family: "SF Pro Display";
  font-size: 16px;
  color: #1ca3ec;
  font-weight: 500;
  line-height: 19px;
  letter-spacing: 0em;
  background-color: white;
}

@media screen and (max-width: 965px) {
  .Wrapper {
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 350px;
    padding: 20px;
    box-shadow: -23px 31px 104px 0px #3e397070;
  }
}
