.Wrapper {
  margin: 10% 0;
}

.Wrapper h2 {
  font-family: "SF Pro Display";
  font-size: 24px;
  font-weight: 500;
  letter-spacing: 0em;
  margin: 3% 0;
}

.Col {
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
  border-right: 1px solid #f5f5f5;
}
.Col h3 {
  font-family: "SF Pro Display";
  font-size: 20px;
  font-weight: 600;
  letter-spacing: 0px;
  margin: 2% 0%;
}

.Col p {
  font-family: "SF Pro Display";
  font-size: 14px;
  font-weight: 400 !important;
  color: #0f2e4a;
  opacity: 0.3;
  width: clamp(100px, 100%, 197px);
  margin: 0 auto;
}

@media screen and (max-width: 770px) {
  .Col:nth-child(1) {
    border-bottom: 1px solid #f5f5f5;
  }
  .Col:nth-child(2) {
    border-bottom: 1px solid #f5f5f5;
  }
  .Col {
    padding: 20px 0;
  }
}

@media screen and (max-width: 500px) {
  .Col {
    border: none !important;
  }
  .Wrapper h2 {
    padding: 0 2%;
  }
}
