.Wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 50px;
  height: 80vh;
}

.texts h3 {
  font-family: "SF Pro Display";
  font-size: 28px;
  font-weight: 500;
  letter-spacing: 0em;
  color: #0f2e4a;
  margin: 3% 0;
}
.texts p {
  margin: 0;
  font-family: "SF Pro Display";
  font-size: 11px;
  color: #0f2e4a;
  font-weight: 400;
  letter-spacing: 0em;
  opacity: 0.3;
}

@media screen and (max-width: 720px) {
  .Wrapper {
    padding: 0 2%;
    display: flex;
    flex-direction: column;
  }
}
