.Wrapper form {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.Wrapper form div {
  display: flex;
  flex-direction: column;
}
.Wrapper p {
  color: red;
}

.Wrapper input {
  padding: 10px 10px !important;
  border-radius: 14px !important;
  border: 1px solid #eeeeee !important;
  outline: none !important;
}
.Wrapper button {
  border: 1.8px solid #1CA3EC;
  padding: 10px 0px 13px;
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0em;
  color: #1CA3EC;
  background-color: transparent;
  border-radius: 14px;
}
