.ant-slider-track-1 {
  background-color: #1CA3EC !important;
}

.ant-slider-handle::after {
  box-shadow: 0 0 0 2px transparent !important;
  border: 3px solid #1CA3EC;
  width: 20px !important;
  top: -50% !important;
  height: 20px !important;
}

.ant-select-selector {
  border: 1px solid #eeeeee !important;
  padding: 5px 25px !important;
  height: 100% !important;
  border-radius: 10px !important;
}
.ant-checkbox-inner {
  background-color: #1CA3EC !important;
  border-color: #1CA3EC !important;
}

.selected {
  border: 2px solid black !important;
  color: black;
}

.previous {
  border: 2px solid #1CA3EC !important;
  padding-right: 1px;
  padding-top: 2px;
  cursor: pointer !important;
}

.next {
  border: 2px solid #1CA3EC !important;
  padding-top: 2px;
  padding-left: 1px;
  cursor: pointer !important;
}

.previous ion-icon {
  font-size: 20px;
  color: #1CA3EC;
}
.next ion-icon {
  font-size: 20px;
  color: #1CA3EC;
}

.disabled {
  opacity: 0.3;
}

:where(.css-dev-only-do-not-override-12upa3x).ant-radio-wrapper
  .ant-radio-checked
  .ant-radio-inner {
  background-color: #1CA3EC !important;
  border: 1px solid #1CA3EC !important;
}
