.userTabs::-webkit-scrollbar {
  display: none;
}

.userTabs {
  overflow-y: scroll;
  margin-top: 50px;
}

.userTabs .ant-tabs-content-holder {
  border: none !important;
}

.userTabs .ant-tabs-ink-bar-animated {
  display: none !important;
}

.userTabs .ant-tabs-left {
  display: contents;
}
.userTabs .ant-tabs-tab {
  padding: 0 !important;
}
.userTabs .ant-tabs-tab-btn {
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0px;
}
.userTabs .ant-tabs-tab-btn {
  border-bottom: 1px solid transparent;
  padding: 0 !important;
}
.userTabs .ant-tabs-tab-btn:hover {
  color: black !important;
}
.userTabs .ant-tabs-tab-active .ant-tabs-tab-btn {
  color: #1CA3EC !important;
}
.userTabs .ant-tabs-tab-active .ant-tabs-tab-btn span {
  border-bottom: 1px solid #1CA3EC;
  color: #1CA3EC;
}
.userTabs p span {
  border-bottom: 1px solid transparent;
  transition: 0.65s;
}
.userTabs p {
  margin: 0;
  display: flex;
  align-items: center;
  gap: 10px;
}

@media screen and (max-width: 975px) {
  .userTabs {
    margin: 0 !important;
  }
  .userTabs .ant-tabs-tab-btn {
    border: 1px solid #eeeeee !important;
    padding: 10px 10px !important;
    margin: 0px !important;
    border-radius: 10px;
  }
  .userTabs
    :where(.css-dev-only-do-not-override-12upa3x).ant-tabs
    .ant-tabs-tab
    + .ant-tabs-tab {
    margin: 10px !important;
  }
  .userTabs .ant-tabs-tab-active .ant-tabs-tab-btn {
    border: 1px solid #1CA3EC !important;
    color: #1CA3EC;
  }
  .userTabs .ant-tabs-tab-active .ant-tabs-tab-btn span {
    border-bottom: 1px solid transparent;
  }
}
