.Wrapper svg {
  width: 20px;
  height: 20px;
}
.WrapperBack {
  border-bottom: 1px solid #eeeeee;
  padding: 1% 0;
}
.Wrapper img {
  width: 130px;
}
.Wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.Wrapper .search {
  display: flex;
  gap: 10px;
}

.close {
  position: fixed !important;
  width: 100%;
  height: 100%;
  background-color: transparent;
  z-index: 10000;
}

.search button {
  border: 1px solid #1ca3ec;
  color: #1ca3ec;
  border-radius: 14px;
  padding: 10px 22px;
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 500;
  line-height: 19px;
  letter-spacing: 0.01em;
  background-color: transparent;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.search input {
  border: 1px solid #e1dfdf;
  padding: 12px 20px;
  border-radius: 14px;
  outline: none;
}

.lastes {
  display: flex;
  align-items: center;
  gap: 25px;
}
.lastes li a {
  text-decoration: none;
  color: black;
  display: flex;
  align-items: center;
  gap: 10px;
}

.langLi {
  display: flex;
  align-items: center;
  gap: 6px;
}
.langLi select option {
  padding: 10px !important;
}

.lastes select {
  border: none;
  padding: 0px;
  background-color: transparent;
  -webkit-appearance: none;
  font-size: 16px;
  font-weight: 500;
  line-height: 17px;
  letter-spacing: 0em;
}
.lastes li p {
  color: #1ca3ec;
}

.lastes li button {
  display: flex;
  align-items: center;
  gap: 10px;
  background-color: white;
  border: 1px solid #eeeeee;
  padding: 10px 20px;
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0em;
  border-radius: 14px;
}
.WarpperCategory ul {
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.lastes li:nth-child(1) {
  display: none;
}

.MediaSearch {
  display: none;
}

.searchBox {
  position: relative;
}
.searchDiv {
  position: absolute;
  background-color: white;
  box-shadow: -23px 31px 104px 0px #3e39701f;
  padding: 15%;
  z-index: 100000;
  margin-top: 10px;
  width: 150px;
  border-radius: 30px;
}
.searchResult h2 {
  font-family: "SF Pro Display";
  font-size: 16px;
  font-weight: 500;
}
.searchResult p {
  font-family: "SF Pro Display";
  font-size: 11px;
  font-weight: 400;
  color: #1ca3ec;
}
.searchResult a {
  text-decoration: none;
  color: black;
}
.user {
  border: 1px solid #eeeeee;
  border-radius: 50%;
  width: 35px;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.user ion-icon {
  width: 20px;
  height: 20px;
}
.MediasearchDiv {
  display: none;
}
@media screen and (max-width: 965px) {
  .WrapperBack {
    border-bottom: none;
    padding: 2% 2%;
  }
  .search {
    display: none !important;
  }
  .lastes li:nth-child(1) {
    display: block;
  }
  .lastes li:nth-child(3) {
    display: none;
  }
  .lastes li:nth-child(4) {
    display: none;
  }
  .lastes li:nth-child(5) {
    display: none;
  }
  .MediaSearch {
    display: block;
  }
  .MediaSearch {
    display: flex;
    gap: 10px;
    padding: 2% 0;
  }
  .MediaSearch svg {
    width: 20px;
    height: 20px;
  }
  .MediaSearch button {
    border: 1px solid #1ca3ec;
    color: #1ca3ec;
    border-radius: 14px;
    padding: 10px 22px;
    font-family: "SF Pro Display";
    font-size: 16px;
    font-weight: 500;
    line-height: 19px;
    letter-spacing: 0.01em;
    background-color: transparent;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .MediaSearch input {
    border: 1px solid #e1dfdf;
    padding: 12px 20px;
    border-radius: 14px;
    width: 100%;
    outline: none;
  }
  .MediaSearch li:nth-child(2) {
    width: 100%;
  }
  .mediaLi {
    position: relative;
  }
  .MediasearchDiv {
    position: absolute;
    display: block !important;
    width: 200px;
    background-color: white;
    box-shadow: -23px 31px 104px 0px #3e39701f;
    z-index: 100000;
    border-radius: 20px;
    padding: 5% !important;
    margin-top: 10px;
  }
}

@media screen and (max-width: 500px) {
  .MediaSearch {
    padding: 4% 0;
  }
  .MediaSearch button span {
    display: none;
  }
  .MediaSearch button {
    padding: 6px 10px;
  }
  .MediaSearch button svg {
    width: 30px;
    height: 30px;
  }
}
